.wp-block-frontis-blocks-container {
	display: flex;
	position: relative;
	box-sizing: border-box;
	transition-property: box-shadow;
	transition-duration: 0.2s;
	transition-timing-function: ease;

	&.frontis-blocks-is-root-container {
		margin-left: auto;
		margin-right: auto;
	}

	&.frontis-blocks-is-root-container .frontis-blocks-container-inner-blocks-wrap {
		display: flex;
		position: relative;
		box-sizing: border-box;
		margin-left: auto !important;
		margin-right: auto !important;
	}

	// Handeling Edge case for blockquote
	.wp-block-frontis-blocks-blockquote {
		margin: unset;
	}

	// Background Video CSS Start.
	.frontis-blocks-container__video-wrap {
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		position: absolute;
		overflow: hidden;
		-webkit-transition: opacity 1s;
		-o-transition: opacity 1s;
		transition: opacity 1s;

		video {
			max-width: 100%;
			width: 100%;
			height: 100%;
			margin: 0;
			line-height: 1;
			border: none;
			display: inline-block;
			vertical-align: baseline;
			-o-object-fit: cover;
			object-fit: cover;
			background-size: cover;
		}
	}

	// Background Video CSS End.
}

// Css with grid layout.
.wp-block-frontis-blocks-container.frontis-blocks-layout-grid {
	display: grid;
	width: 100%;

	> .frontis-blocks-container-inner-blocks-wrap {
		display: inherit;
		width: inherit;
		> .wp-block-frontis-blocks-container {
			max-width: unset !important;
			width: unset !important;
		}
	}

	> .wp-block-frontis-blocks-container {
		max-width: unset !important;
		width: unset !important;
	}

	&.frontis-blocks-is-root-container {
		margin-left: auto;
		margin-right: auto;
		> .wp-block-frontis-blocks-container {
			max-width: unset !important;
			width: unset !important;
		}
	}

	&.alignwide.frontis-blocks-is-root-container {
		margin-left: auto;
		margin-right: auto;
	}

	&.frontis-blocks-is-root-container .frontis-blocks-container-inner-blocks-wrap {
		display: inherit;
		position: relative;
		box-sizing: border-box;
		margin-left: auto !important;
		margin-right: auto !important;
	}
}

body .wp-block-frontis-blocks-container > .frontis-blocks-container-inner-blocks-wrap > *:not( .wp-block-frontis-blocks-container ):not( .wp-block-frontis-blocks-column ):not( .wp-block-frontis-blocks-container ):not( .wp-block-frontis-blocks-section ):not( .frontis-blocks-container__shape ):not( .frontis-blocks-container__video-wrap ):not(.wp-block-frontis-blocks-info-box),
body .wp-block-frontis-blocks-container > .frontis-blocks-container-inner-blocks-wrap,
body .wp-block-frontis-blocks-container > *:not( .wp-block-frontis-blocks-container ):not( .wp-block-frontis-blocks-column ):not( .wp-block-frontis-blocks-container ):not( .wp-block-frontis-blocks-section ):not( .frontis-blocks-container__shape ):not( .frontis-blocks-container__video-wrap ):not( .frontis-blocks-slider-container ):not(.wp-block-frontis-blocks-lottie):not(.frontis-blocks-faq__outer-wrap) {
	min-width: unset !important;
	// width: 100%;
	position: relative;
	z-index: 1;
}

body .wp-block-frontis-blocks-container > .frontis-blocks-container-inner-blocks-wrap > .wp-block-frontis-blocks-container,
body .wp-block-frontis-blocks-container > .frontis-blocks-container-inner-blocks-wrap {
	> ul,
	ol {
		max-width: -webkit-fill-available;
		margin-block-start: 0;
		margin-block-end: 0;
		margin-left: 20px;
	}
}

// Shape Divider CSS Start.
.frontis-blocks-container__shape {
	overflow: hidden;
	position: absolute;
	left: 0;
	width: 100%;
	line-height: 0;
	direction: ltr;
}

.frontis-blocks-container__shape-top {
	top: -3px;
}

.frontis-blocks-container__shape-bottom {
	bottom: -3px;
}

.frontis-blocks-container__shape.frontis-blocks-container__invert.frontis-blocks-container__shape-bottom,
.frontis-blocks-container__shape.frontis-blocks-container__invert.frontis-blocks-container__shape-top {
	-webkit-transform: rotate(180deg);
	-ms-transform: rotate(180deg);
	transform: rotate(180deg);
}

.frontis-blocks-container__shape.frontis-blocks-container__shape-flip svg {
	transform: translateX(-50%) rotateY(180deg);
}

.frontis-blocks-container__shape svg {
	display: block;
	width: -webkit-calc(100% + 1.3px);
	width: calc(100% + 1.3px);
	position: relative;
	left: 50%;
	-webkit-transform: translateX(-50%);
	-ms-transform: translateX(-50%);
	transform: translateX(-50%);
}

.frontis-blocks-container__shape .frontis-blocks-container__shape-fill {
	-webkit-transform-origin: center;
	-ms-transform-origin: center;
	transform-origin: center;
	-webkit-transform: rotateY(0deg);
	transform: rotateY(0deg);
}

.frontis-blocks-container__shape.frontis-blocks-container__shape-above-content {
	z-index: 9;
	pointer-events: none;
}
// Shape Divider CSS End.

//Neve Theme Compatibility
.nv-single-page-wrap .nv-content-wrap.entry-content {

	.wp-block-frontis-blocks-container {
		margin-left: calc(50% - 50vw);
		margin-right: calc(50% - 50vw);
	}
}


.wp-block-frontis-blocks-container p {
	margin: 0;
}
//Heading block compatibility
@media only screen and (max-width: 767px) {
	.wp-block-frontis-blocks-container .wp-block-frontis-blocks-advanced-heading {
		width: -webkit-fill-available;
	}
}
// Handle backward case where Image block aligns to left by default.
.wp-block-frontis-blocks-image--align-none {
	justify-content: center;
}